import React from "react";
import { path } from "ramda";

import BlogIntro from "components/blog/BlogIntro.jsx";
import BlogPreview from "components/blog/blogPreview";
import Layout from "components/layout";
import BlogEmailInput from "components/blog/BlogEmailInput.jsx";
import blogPreviewImage from "assets/img/the-log-preview-image.png";

/*
 * This page controls what someone sees when they go to /blog
 * It assumes a json of all of the ***published*** posts from contentful interface (of all types)
 * have been
 * It queries for all of the ***published*** posts from the contentful graphql interface
 * and shows each of the posts as a snapshot
 * There are multiple types of posts in Contentful (i.e. different templates/data models)
 * so we have to query for each one, though we assume they follow the format in the query
 * We assume that there is some system (i.e. webhook) that notifies the website when someone
 * publishes a blog post or deletes a blog post so the production website can be updated
 * If running locally, make sure to run yarn start again if changes in contentful are made
 * There is one top blog post (BlogIntro), then the rest of BlogPreviews
 */

// TODO: Decide on how we want to order the posts
// IMPORTANT: Assumes only one blog post is is_top_post across all nodes. TODO: enforce this
const createBlogList = (allNodes) => {
  // get list of all top posts that should be shown. Choose one
  const topPosts = allNodes
    .filter(({ fields }) => fields.isTopPost === true)
    .map((item) => ({ id: item.sys.id, fields: item.fields }));
  let chosenTopPost = null;
  let posts = allNodes;
  // chooses the top post if multiple - and removes the top post from the blog previews
  if (topPosts.length > 0) {
    chosenTopPost = topPosts[0];
    posts = posts.filter((item) => item.sys.id !== chosenTopPost.id);
  }
  // return contents
  return (
    <div>
      {chosenTopPost && (
        <BlogIntro
          imageOnTheBack={
            chosenTopPost.fields.heroImage
              ? chosenTopPost.fields.heroImage.fields.file.url
              : undefined
          }
          title={chosenTopPost.fields.title}
          link={chosenTopPost.fields.urlPath}
          subtitle={chosenTopPost.fields.subtitle}
        />
      )}
      <div className="blog-posts">
        {posts.map(({ fields }) => {
          const desktopImage = path(
            ["heroImage", "fields", "file", "url"],
            fields,
          );

          const thumbnailImage = path(
            ["thumbnailImage", "fields", "file", "url"],
            fields,
          );

          const thumbnailImageRetina = path(
            ["thumbnailImageRetina", "fields", "file", "url"],
            fields,
          );

          return (
            <BlogPreview
              // Adding the desktop image as a fallback for the thumbnail image
              // for backwards compatibility
              thumbnailImage={thumbnailImage || desktopImage}
              thumbnailImageRetina={thumbnailImageRetina || desktopImage}
              postTitle={fields.title}
              postDate={fields.date}
              postCategory={fields.category.fields.title}
              postLink={fields.urlPath}
              postAuthor={fields.author}
              key={fields.urlPath}
            />
          );
        })}
      </div>
    </div>
  );
};

export default ({ location, pageContext }) => (
  <Layout
    whiteContentBackground
    location={location}
    title="The Log | Brex"
    description="Covering finance, business, accounting, and more, catch up on the latest guides and insights from the Brex team in The Log"
    ogTitle="The Log | Brex"
    ogDescription="Covering finance, business, accounting, and more, catch up on the latest guides and insights from the Brex team in The Log"
    ogImage={blogPreviewImage}
  >
    <div className="section__light-blog">
      {createBlogList(pageContext.contentfulPosts)}
      {<BlogEmailInput backgroundColor="gray" />}
    </div>
  </Layout>
);
