import React from "react";

import heroImg from "assets/img/hero-bg-img.jpg";
import {
  ImageIntro,
  IntroWrapper,
  IntroTitle,
  IntroSubtitle,
  IntroActionButton,
  ReadMoreLink,
} from "./elements";

const BlogIntro = ({ imageOnTheBack = heroImg, title, subtitle, link }) => (
  <ImageIntro imageOnTheBack={imageOnTheBack}>
    <IntroWrapper>
      <IntroTitle>{title}</IntroTitle>
      {subtitle && <IntroSubtitle>{subtitle}</IntroSubtitle>}
      <IntroActionButton>
        <ReadMoreLink to={`/blog/${link}`}>Read More</ReadMoreLink>
      </IntroActionButton>
    </IntroWrapper>
  </ImageIntro>
);

export default BlogIntro;
